.tag {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff6060;
  border-radius: 8px;
  margin-right: 10px;
  width: 20%;
  height: 25px;
}

.tagtext {
  color: #fff;
  text-align: center;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

@media screen and (max-width: 600px) {
  .tag {
    width: 30%;
  }
  .tagtext {
    font-size: 10px;
  }
}
